/* COLORS */
.dropdown {
  position: relative;
}

.dropdown__content {
  position: absolute;
  top: 100%;
  z-index: 10;
  padding: 20px;
  display: none;
  opacity: 0;
  will-change: opacity;
  background: #ffffff;
  box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
  transition-property: opacity, box-shadow;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.dropdown_open .dropdown__content {
  display: inline-flex;
}

.dropdown_visible .dropdown__content {
  opacity: 1;
}
